/*-------------
    General
-------------*/

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html{
    font: normal 16px sans-serif;
    color: #555;
}

ul, nav{
    list-style: none;
}

a{
    text-decoration: none;
    color: inherit;
    cursor: pointer;

    opacity: 0.9;
}

a:hover{
    opacity: 1;
}

a.btn{
    color: #fff;
    border-radius: 10px;
    text-transform: uppercase;
    border: 1px solid #FFFFFF;
    font-weight: 800;
    text-align: center;
    transition: 0.3s;
}

a.btn:hover {
    background-color: #FFFFFF;
    color:#006088;
}

hr{
    width: 150px;
    height: 2px;
    background-color: #2196F3;
    border: 0;
    margin-bottom: 80px;
}

section{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 125px 100px;
    padding-top: 60px;
    padding-bottom: 80px;
}

@media (max-width: 1000px){

    section{
        padding: 100px 50px;
    }

}

@media (max-width: 600px){

    section{
        padding: 80px 30px;
    }

}

section h3.title{
    color: #043464;
    text-transform: uppercase;
    font: bold 32px 'Open Sans', sans-serif;
    margin-bottom: 35px;
    text-align: center;
}

section p{
    max-width: 800px;
    text-align: center;
    margin-bottom: 35px;
    padding: 0 20px;
    line-height: 2;
}

/*-------------
    Header
-------------*/

header{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    width: 100%;
    display: flex;
    font-family: 'Helvetica';
    align-items: center;
    color: #043464;
    padding: 35px 100px 0;
    padding-left: 10px;
}

header h2{
    font-family: 'Helvetica', sans-serif;
    padding-right: 10px;
}

header img {
  height: 50px;
  width: 80px;
  margin-left: 20px;
  margin-right: 40px;
}

header nav{
    display: flex;
}

header nav li{
    margin: 0 30px;
    font-weight: bold;
    font-size: 14pt;
}

header nav li:first-child{
    margin-left: 0;
}

header nav li:last-child{
    margin-right: 0;
}



@media (max-width: 1000px){
    header{
        padding: 20px 50px;
    }
}


@media (max-width: 700px){
    header{
        flex-direction: column;
    }

    header img {
        margin-bottom: 30px;
    }

    header h2{
        margin-bottom: 15px;
    }
}



/*----------------
    Hero Section
----------------*/

.hero{
    position: relative;
    justify-content: center;
    min-height: 100vh;
    color: #fff;
    text-align: center;
}

.hero .background-image{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-image: url("https://images.wallpapersden.com/image/download/sea-waves-blur_14644_1920x1200.jpg");
    background-color: #2196F3;
    z-index: -1;
}

.hero .background-image:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #414a4f;
    opacity: 0.30;
}

.hero h1{
    font: bold 60px 'Open Sans', sans-serif;
    font-size: 32pt;
    text-shadow: 0px 2px 5px #808080;
    margin-top: 160px;
    margin-bottom: 35px;
}

.hero h3{
    font: normal 28px 'Open Sans', sans-serif;
    font-weight: bold;
    margin-bottom: 80px;
    font-size: 16pt;
}

.hero a.btn{
    margin-top: 30px;
    padding: 16px 46px;
}

@media (max-width: 800px){

    .hero{
        min-height: 600px;
    }

    .hero h1{
        font-size: 48px;
    }

    .hero h3{
        font-size: 24px;
    }

    .hero a.btn{
        padding: 15px 40px;
    }

}



/*--------------------
    Overview Section
---------------------*/

.overview{
    background-color: #006088;
    color: #FFFFFF;
    padding-left: 50px;
    padding-right: 50px;
}

.overview p {
   text-align: left;
   padding: 0px 0px 0px 0px;
   font-size: 14pt;
   margin-bottom: 0px;
}

.overview h3.title{
    color: #FFFFFF;
}

@media (max-width: 1000px){

}



/*----------------------
    Features Section
----------------------*/

.features{
    background-color: #f7f7f7;
}

.features p {
    font-size: 14pt;
}

@media (max-width: 1000px){

}


/*--------------------
    Team Section
--------------------*/

.team{
    background-color: #fff;
}

.team-banner {
    display: flex;
    width: 100%;
}

.team-card {
    width: 25%;
}

.team-name {
    font-weight: bold;
    font-size: 14pt;
}

.team-card p {
    margin: 0px;
    padding: 0px;
    color: #000000;
}

.team-card img {
    display: block;
    margin: auto;
    margin-bottom: 10px;
    border-radius: 50%;
    width: 150px;
    height: 150px;
    object-fit: cover;
}

@media (max-width: 850px){
    .team-banner {
        flex-direction: column;
        align-items: center;
    }

    .team-card {
        width: 100%;
        text-align: center;
    }

    .team-card img {
        margin-top: 30px;
    }
}



/*---------------------
    Sponsor Section
---------------------*/

.sponsor{
    background-color: #f7f7f7;
}

.sponsor-img {
    width: 280px;
    height: 200px;
}

.sponsor-banner {
    display: flex;
    align-items: center;
}

.sponsor-banner p {
    font-size: 14pt;
    color: #000000;
    text-align: left;
}

.sponsor-banner div {
    width: 50%;
}

@media (max-width: 800px){
    .sponsor-img {
      width: 170px;
      height: 130px;
    }
}

/*-------------
    Footer
-------------*/

footer{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background-color: #006088;
    padding: 40px 0;
}

footer p{
    font-style: italic;
    font-size: 14pt;
    color: #FFFFFF;
}

@media (max-width: 700px){
    footer{
        padding: 80px 15px;
    }
}
